"use client";

import Image from "next/image";
import { usePathname, useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { AlertDialog, AlertDialogAction, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog";
import { InputOTP, InputOTPGroup, InputOTPSlot } from "@/components/ui/input-otp";
import { decryptKey, encryptKey } from "@/lib/utils";
export const PasskeyModal = () => {
  const router = useRouter();
  const path = usePathname();
  const [open, setOpen] = useState(false);
  const [passkey, setPasskey] = useState("");
  const [error, setError] = useState("");
  const encryptedKey = typeof window !== "undefined" ? window.localStorage.getItem("accessKey") : null;
  useEffect(() => {
    const accessKey = encryptedKey && decryptKey(encryptedKey);
    if (path) if (accessKey === process.env.NEXT_PUBLIC_ADMIN_PASSKEY!.toString()) {
      setOpen(false);
      router.push("/admin");
    } else {
      setOpen(true);
    }
  }, [encryptedKey]);
  const closeModal = () => {
    setOpen(false);
    router.push("/");
  };
  const validatePasskey = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    if (passkey === process.env.NEXT_PUBLIC_ADMIN_PASSKEY) {
      const encryptedKey = encryptKey(passkey);
      localStorage.setItem("accessKey", encryptedKey);
      setOpen(false);
    } else {
      setError("Invalid passkey. Please try again.");
    }
  };
  return <AlertDialog open={open} onOpenChange={setOpen} data-sentry-element="AlertDialog" data-sentry-component="PasskeyModal" data-sentry-source-file="PasskeyModal.tsx">
      <AlertDialogContent className="shad-alert-dialog" data-sentry-element="AlertDialogContent" data-sentry-source-file="PasskeyModal.tsx">
        <AlertDialogHeader data-sentry-element="AlertDialogHeader" data-sentry-source-file="PasskeyModal.tsx">
          <AlertDialogTitle className="flex items-start justify-between" data-sentry-element="AlertDialogTitle" data-sentry-source-file="PasskeyModal.tsx">
            Admin Access Verification
            <Image src="/assets/icons/close.svg" alt="close" width={20} height={20} onClick={() => closeModal()} className="cursor-pointer" data-sentry-element="Image" data-sentry-source-file="PasskeyModal.tsx" />
          </AlertDialogTitle>
          <AlertDialogDescription data-sentry-element="AlertDialogDescription" data-sentry-source-file="PasskeyModal.tsx">
            To access the admin page, please enter the passkey.
          </AlertDialogDescription>
        </AlertDialogHeader>
        <div>
          <InputOTP maxLength={6} value={passkey} onChange={value => setPasskey(value)} data-sentry-element="InputOTP" data-sentry-source-file="PasskeyModal.tsx">
            <InputOTPGroup className="shad-otp" data-sentry-element="InputOTPGroup" data-sentry-source-file="PasskeyModal.tsx">
              <InputOTPSlot className="shad-otp-slot" index={0} data-sentry-element="InputOTPSlot" data-sentry-source-file="PasskeyModal.tsx" />
              <InputOTPSlot className="shad-otp-slot" index={1} data-sentry-element="InputOTPSlot" data-sentry-source-file="PasskeyModal.tsx" />
              <InputOTPSlot className="shad-otp-slot" index={2} data-sentry-element="InputOTPSlot" data-sentry-source-file="PasskeyModal.tsx" />
              <InputOTPSlot className="shad-otp-slot" index={3} data-sentry-element="InputOTPSlot" data-sentry-source-file="PasskeyModal.tsx" />
              <InputOTPSlot className="shad-otp-slot" index={4} data-sentry-element="InputOTPSlot" data-sentry-source-file="PasskeyModal.tsx" />
              <InputOTPSlot className="shad-otp-slot" index={5} data-sentry-element="InputOTPSlot" data-sentry-source-file="PasskeyModal.tsx" />
            </InputOTPGroup>
          </InputOTP>

          {error && <p className="shad-error text-14-regular mt-4 flex justify-center">
              {error}
            </p>}
        </div>
        <AlertDialogFooter data-sentry-element="AlertDialogFooter" data-sentry-source-file="PasskeyModal.tsx">
          <AlertDialogAction onClick={e => validatePasskey(e)} className="shad-primary-btn w-full" data-sentry-element="AlertDialogAction" data-sentry-source-file="PasskeyModal.tsx">
            Enter Admin Passkey
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>;
};